/* Google fonts */
@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,200;0,400;0,500;1,200;1,400;1,500&family=Rubik+Scribble&display=swap');

/* Variables css */
:root {
  --header-height: 3rem;

  /* Colors */
  --hue: 0;
  --sat: 0%;
  --title-color: #1839a4;
  --text-color: #032143;
  --body-color: #000000;
  --container-color: #fffdb4;

  /* Fonts & typography*/
  --heading-font: 'Rubik Scribble', cursive;
  --body-font: 'Poppins', sans-serif;
  --big-font-size: 3.5rem;
  --h3-font-size: 1.25rem;
  --normal-font-size: 1rem;
  --small-font-size: 0.875rem;
  --smaller-font-size: 0.813rem;
  --tiny-font-size: 0.625rem;

  /* Font weight */
  --font-normal: 400;
  --font-medium: 500;
  --font-semi-bold: 600;
}

::selection {
  background-color: #ad1d1d;
  color: white;
}

/* ============ GLOBAL STYLES ============ */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  cursor: url('./cursor.png') 10 10, auto;
}

html {
  scroll-behavior: smooth;
}

body {
  background-color: var(--body-color);
  color: var(--text-color);
  font-family: var(--body-font);
  font-size: var(--normal-font-size);
  font-weight: var(--font-normal);
}

.App {
  width: 100%;
  overflow-x: hidden;
}

a {
  text-decoration: none;
  color: inherit;
}

button {
  border: none;
  cursor: pointer;
  font-family: inherit;
}

/* ============ UTILITY CLASSES ============ */
.section {
  padding: 4rem 0;
}

.section__title {
  font-family: var(--heading-font);
  font-size: var(--big-font-size);
  color: var(--title-color);
  text-align: center;
  margin-bottom: 3rem;
  letter-spacing: 0.5px;
}

/* ============ HEADER & NAVIGATION ============ */
.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background-color: var(--container-color);
  z-index: 1000;
  box-shadow: 0 2px 10px rgba(24, 57, 164, 0.1);
  height: var(--header-height);
  display: flex;
  align-items: center;
}

.nav__container {
  max-width: 1200px;
  margin: 0 auto;
  width: 100%;
  padding: 0 1.5rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.nav__logo {
  font-family: var(--heading-font);
  font-size: var(--h3-font-size);
  color: var(--title-color);
  font-weight: var(--font-semi-bold);
}

.nav__logo-text {
  display: inline-block;
  transition: transform 0.3s ease;
}

.nav__logo:hover .nav__logo-text {
  transform: scale(1.1);
}

nav {
  display: flex;
  gap: 2rem;
  align-items: center;
}

.nav__link {
  font-family: var(--body-font);
  font-size: var(--normal-font-size);
  color: var(--text-color);
  font-weight: var(--font-medium);
  position: relative;
  transition: color 0.3s ease;
}

.nav__link::after {
  content: '';
  position: absolute;
  bottom: -5px;
  left: 0;
  width: 0;
  height: 2px;
  background-color: var(--title-color);
  transition: width 0.3s ease;
}

.nav__link:hover::after,
.nav__link.active::after {
  width: 100%;
}

.nav__link:hover {
  color: var(--title-color);
}

.nav__toggle {
  display: none;
  font-size: 1.5rem;
  color: var(--title-color);
  cursor: pointer;
}

.nav__toggle i {
  transition: transform 0.3s ease;
}

/* ============ HOME SECTION ============ */

.home {
  padding-top: 7rem;
  min-height: 100vh;
  display: flex;
  align-items: center;
  background: var(--container-color);
}

.home__container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 1.5rem;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 3rem;
  align-items: center;
}

.home__content {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.home__title {
  font-family: var(--heading-font);
  font-size: var(--big-font-size);
  color: var(--title-color);
  line-height: 1.2;
  animation: fadeInUp 0.8s ease;
}

.home__subtitle {
  font-family: var(--body-font);
  font-size: var(--h3-font-size);
  color: var(--text-color);
  font-weight: var(--font-semi-bold);
  animation: fadeInUp 0.8s ease 0.1s backwards;
}

.home__description {
  font-family: var(--body-font);
  font-size: var(--normal-font-size);
  color: var(--text-color);
  font-weight: var(--font-normal);
  line-height: 1.6;
  max-width: 500px;
  animation: fadeInUp 0.8s ease 0.2s backwards;
}

.home__buttons {
  display: flex;
  gap: 1.5rem;
  animation: fadeInUp 0.8s ease 0.3s backwards;
  flex-wrap: wrap;
}

.button {
  padding: 1rem 2rem;
  font-family: var(--body-font);
  font-size: var(--normal-font-size);
  font-weight: var(--font-medium);
  border-radius: 50px;
  transition: all 0.3s ease;
  display: inline-block;
  text-align: center;
}

.button__primary {
  background-color: var(--title-color);
  color: white;
  box-shadow: 0 4px 15px rgba(24, 57, 164, 0.3);
}

.button__primary:hover {
  transform: translateY(-3px);
  box-shadow: 0 6px 20px rgba(24, 57, 164, 0.4);
}

.button__secondary {
  background-color: transparent;
  color: var(--title-color);
  border: 2px solid var(--title-color);
}

.button__secondary:hover {
  background-color: var(--title-color);
  color: white;
  transform: translateY(-3px);
}

.home__socials {
  display: flex;
  gap: 1.5rem;
  animation: fadeInUp 0.8s ease 0.4s backwards;
}

.social__link {
  width: 50px;
  height: 50px;
  background-color: var(--title-color);
  color: white;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.5rem;
  transition: all 0.3s ease;
  box-shadow: 0 4px 10px rgba(24, 57, 164, 0.2);
}

.social__link:hover {
  transform: translateY(-5px) scale(1.1);
  box-shadow: 0 6px 15px rgba(24, 57, 164, 0.4);
  background-color: var(--container-color);
  color: var(--title-color);
}

.home__image {
  display: flex;
  align-items: center;
  justify-content: center;
  animation: float 3s ease-in-out infinite;
}

.home__img{
  width: 100%;
  height: 100%;
  border-radius: 63% 37% 54% 46% / 55% 48% 52% 45%;
  animation: blobAnimation 6s ease-in-out infinite;
  box-shadow: 0 10px 40px rgba(24, 57, 164, 0.2);
  object-fit: cover;
  display: block;
}

/* ============ BLOG SECTION ============ */
.blog__section {
  background-color: var(--text-color);
  padding: 4rem 0;
}
.slideshow__container {
  position: relative;
  width: 90%;
  max-width: 1000px;
  aspect-ratio: 16 / 9;
  margin: 2rem auto;
  background-color: #fff;
  border: 2vw solid #333;
  border-radius: 20px;
  box-shadow: 
    0 0 0 1px #000,
    5px 5px 15px rgba(0, 0, 0, 0.2);
  overflow: hidden;
}

/* Slides */
.mySlides {
  position: relative;
  width: 100%;
  height: 100%;
}

.mySlides img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
}

.numbertext {
  position: absolute;
  top: 2%;
  left: 2%;
  color: white;
  font-size: clamp(10px, 2vw, 16px);
  padding: 0.5rem 1rem;
  background-color: rgba(0, 0, 0, 0.5);
  border-radius: 8px;
  z-index: 5;
}

.text {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  color: white;
  background-color: rgba(0, 0, 0, 0.6);
  padding: clamp(10px, 3vw, 20px);
  text-align: center;
  font-size: clamp(14px, 2.5vw, 18px);
  margin: 0;
}

/* Navigation Buttons */
.prev, .next {
  cursor: pointer;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: clamp(40px, 8vw, 60px);
  height: clamp(40px, 8vw, 60px);
  color: white;
  background-color: rgba(0, 0, 0, 0.6);
  font-weight: bold;
  font-size: clamp(16px, 4vw, 28px);
  transition: all 0.3s ease;
  border-radius: 10px;
  border: none;
  user-select: none;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 10;
  padding: 0;
}

.prev {
  left: clamp(10px, 3vw, 20px);
}

.next {
  right: clamp(10px, 3vw, 20px);
}

.prev:hover, .next:hover {
  background-color: rgba(0, 0, 0, 0.8);
  transform: translateY(-50%) scale(1.1);
}

/* Fade Animation */
.fade {
  animation: fade 1s;
}

@keyframes fade {
  from {
    opacity: 0.4;
  }
  to {
    opacity: 1;
  }
}

/* Dots/Indicators */
.dot {
  cursor: pointer;
  height: clamp(8px, 1.5vw, 14px);
  width: clamp(8px, 1.5vw, 14px);
  margin: 0.5rem clamp(4px, 1vw, 8px);
  background-color: #bbb;
  border-radius: 50%;
  display: inline-block;
  transition: background-color 0.3s ease;
}

.dot:hover {
  background-color: #717171;
}

/* Responsive adjustments for mobile */
@media (max-width: 768px) {
  .slideshow__container {
    width: 95%;
    aspect-ratio: 16 / 9;
    margin: 1.5rem auto;
  }
}

@media (max-width: 480px) {
  .slideshow__container {
    width: 100%;
    aspect-ratio: 4 / 3;
    border-radius: 15px;
    margin: 1rem 0;
  }
}

/* ============ ABOUT SECTION ============ */
.about {
  background-color: var(--container-color);
  padding: 4rem 0;
}

.about__container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 1.5rem;
}

.about__content {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

.about__heading {
  font-family: var(--body-font);
  font-size: var(--h3-font-size);
  color: var(--title-color);
  font-weight: var(--font-semi-bold);
}

.about__text {
  font-family: var(--body-font);
  font-size: var(--normal-font-size);
  color: var(--text-color);
  line-height: 1.8;
  text-align: justify;
}

/* SKILLS STYLES */

.skills__container {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  margin-top: 2rem;
}

.skills__category {
  background-color: var(--text-color);
  border: 5px solid #ad1d1d;
  border-radius: 15px;
  padding: 2rem;
  box-shadow: 0 4px 15px rgba(196, 30, 58, 0.1);
  animation: fadeInUp 0.6s ease-out;
}

.skills__category-title {
  font-size: 1.1rem;
  font-weight: 600;
  margin-bottom: 1.5rem;
  color: white;
  text-transform: uppercase;
  letter-spacing: 1px;
  position: relative;
  padding-bottom: 0.8rem;
}

.skills__category-title::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 60px;
  height: 3px;
  background: #ad1d1d;
  border-radius: 2px;
}

.skills__items {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
}

.skill__item {
  background-color: #ad1d1d;
  padding: 0.9rem 1.5rem;
  border-radius: 20px;
  border: 5px solid #5b0f0f;
  transition: all 0.3s cubic-bezier(0.25, 0.46, 0.45, 0.94);
  box-shadow: 0 2px 8px rgba(196, 30, 58, 0.08);
  display: inline-block;
  color: white;
}

.skill__item:hover {
  transform: translateY(-4px) scale(1.05);
  background-color: white;
  box-shadow: 0 8px 25px rgba(196, 30, 58, 0.25);
}

.skill__item:hover .skill__name {
  color: #ad1d1d;
}

.skill__name {
  font-family: var(--body-font);
  font-size: 0.95rem;
  color: white;
  font-weight: 500;
  display: block;
  text-align: center;
  white-space: nowrap;
  transition: color 0.3s ease;
}

/* Responsive Design */
@media (max-width: 768px) {
  .skills__container {
    gap: 1.5rem;
  }

  .skills__category {
    padding: 1.5rem;
  }

  .skills__category-title {
    font-size: 1rem;
  }

  .skill__item {
    padding: 0.7rem 1.2rem;
    font-size: 0.9rem;
  }

  .skills__category-title::after {
    width: 50px;
  }
}

@media (max-width: 480px) {
  .skills__container {
    gap: 1rem;
  }

  .skills__category {
    padding: 1.2rem;
    border-radius: 12px;
  }

  .skills__items {
    gap: 0.8rem;
  }

  .skill__item {
    padding: 0.6rem 1rem;
    font-size: 0.85rem;
  }

  .skill__name {
    font-size: 0.85rem;
  }
}

/* Animation */
@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* ============ PORTFOLIO TABS ============ */
.projects{
  background-color: var(--text-color);
  padding: 4rem 0;
}

.portfolio__tabs {
  display: flex;
  justify-content: center;
  gap: 1rem;
  margin-bottom: 3rem;
  flex-wrap: wrap;
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
  padding: 0 1.5rem;
}

.portfolio__tab {
  font-family: var(--body-font);
  font-size: var(--normal-font-size);
  font-weight: var(--font-medium);
  color: var(--text-color);
  background-color: white;
  border: 2px solid var(--title-color);
  padding: 0.75rem 2rem;
  border-radius: 50px;
  cursor: pointer;
  transition: all 0.3s ease;
  position: relative;
}

.portfolio__tab:hover {
  background-color: var(--title-color);
  color: white;
  transform: translateY(-3px);
  box-shadow: 0 4px 15px rgba(24, 57, 164, 0.3);
}

.portfolio__tab.active {
  background-color: var(--title-color);
  color: white;
  box-shadow: 0 6px 20px rgba(24, 57, 164, 0.4);
}

/* ============ PDF VIEWER CONTAINER ============ */
.pdf__viewer-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 1.5rem;
  display: flex;
  flex-direction: column;
  gap: 2rem;
  align-items: center;
}

.pdf__viewer {
  width: 100%;
  max-width: 900px;
  aspect-ratio: 8.5 / 11;
  background: white;
  border-radius: 10px;
  box-shadow: 0 10px 40px #000000;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 2px solid #e0e0e0;
  position: relative;
  overflow: hidden;
  transition: all 0.3s ease;
}

.pdf__viewer:hover {
  box-shadow: 0 15px 50px rgba(24, 57, 164, 0.3);
}

.pdf__placeholder {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 1rem;
  color: var(--title-color);
}

.pdf__placeholder i {
  font-size: 4rem;
  opacity: 0.5;
}

.pdf__placeholder p {
  font-family: var(--body-font);
  font-size: var(--normal-font-size);
  color: var(--text-color);
  margin: 0;
}

.pdf__hint {
  font-size: var(--small-font-size) !important;
  color: var(--title-color) !important;
  font-weight: var(--font-medium);
  margin-top: 1rem;
}

/* ============ RESPONSIVE PDF VIEWER ============ */
@media screen and (max-width: 768px) {
  .portfolio__tabs {
    gap: 0.75rem;
  }

  .portfolio__tab {
    padding: 0.6rem 1.5rem;
    font-size: var(--small-font-size);
  }

  .pdf__viewer {
    max-width: 100%;
    aspect-ratio: 8.5 / 11;
  }

  .pdf__controls {
    gap: 1rem;
  }

  .pdf__button {
    padding: 0.6rem 1rem;
    font-size: var(--small-font-size);
  }

  .pdf__page-info {
    padding: 0.6rem 1rem;
    font-size: var(--small-font-size);
  }

  .pdf__input {
    width: 40px;
    font-size: var(--smaller-font-size);
  }
}

@media screen and (max-width: 480px) {
  .portfolio__tabs {
    gap: 0.5rem;
    margin-bottom: 2rem;
  }

  .portfolio__tab {
    padding: 0.5rem 1rem;
    font-size: var(--smaller-font-size);
  }

  .pdf__viewer-container {
    gap: 1.5rem;
  }

  .pdf__viewer {
    aspect-ratio: 8.5 / 11;
  }

  .pdf__controls {
    gap: 0.75rem;
    width: 100%;
  }

  .pdf__button {
    padding: 0.5rem 0.75rem;
    font-size: var(--smaller-font-size);
    flex: 1;
  }

  .pdf__page-info {
    padding: 0.5rem 0.75rem;
    font-size: var(--tiny-font-size);
    flex-basis: 100%;
    order: 3;
  }

  .pdf__placeholder p {
    font-size: var(--small-font-size);
  }

  .pdf__placeholder i {
    font-size: 3rem;
  }
}

/* ============ CONTACT SECTION ============ */
.contact {
  background-color: var(--container-color);
  padding: 4rem 0;
}

.contact__container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 1.5rem;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 3rem;
  align-items: start;
}

.contact__info {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

.contact__heading {
  font-family: var(--body-font);
  font-size: var(--h3-font-size);
  color: var(--title-color);
  font-weight: var(--font-semi-bold);
}

.contact__text {
  font-family: var(--body-font);
  font-size: var(--normal-font-size);
  color: var(--text-color);
  line-height: 1.8;
}

.contact__details {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.contact__item {
  display: flex;
  gap: 1.5rem;
  align-items: flex-start;
}

.contact__item i {
  font-size: 2rem;
  color: var(--title-color);
  flex-shrink: 0;
}

.contact__item h4 {
  font-family: var(--body-font);
  font-size: var(--normal-font-size);
  color: var(--title-color);
  font-weight: var(--font-semi-bold);
  margin-bottom: 0.25rem;
}

.contact__item p {
  font-family: var(--body-font);
  font-size: var(--small-font-size);
  color: var(--text-color);
}

.contact__img{
  width: 100%;
  height: auto;
  object-fit: cover;
}

/* ============ FOOTER ============ */
.footer {
  background-color: var(--body-color);
  color: var(--container-color);
  padding: 2rem 0;
  border-top: 2px solid var(--title-color);
}

.footer__content {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 1.5rem;
  text-align: center;
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.footer__content p {
  font-family: var(--body-font);
  font-size: var(--small-font-size);
}

/* ============ ANIMATIONS ============ */
@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes float {
  0%, 100% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(-20px);
  }
}

@keyframes blobAnimation {
  0%, 100% {
    border-radius: 63% 37% 54% 46% / 55% 48% 52% 45%;
  }
  50% {
    border-radius: 37% 63% 46% 54% / 48% 55% 45% 52%;
  }
}

/* ============ RESPONSIVE DESIGN ============ */
@media screen and (max-width: 768px) {
  .nav__toggle {
    display: block;
  }

  nav {
    position: absolute;
    top: 100%;
    left: 0;
    right: 0;
    background-color: var(--container-color);
    flex-direction: column;
    gap: 0;
    display: none;
    width: 100%;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
  }

  nav.nav__mobile {
    display: flex;
  }

  .nav__link {
    padding: 1rem 1.5rem;
    border-bottom: 1px solid #ddd;
    width: 100%;
  }

  .home__container {
    grid-template-columns: 1fr;
  }

  .home__title {
    font-size: 2.5rem;
  }

  .image__blob {
    width: 50%;
    height: 50%;
  }

  .contact__container {
    grid-template-columns: 1fr;
  }

  .section {
    padding: 2.5rem 0;
  }

  .section__title {
    font-size: 2rem;
  }

  .skills__container {
    grid-template-columns: repeat(auto-fit, minmax(120px, 1fr));
  }

  .portfolio__container {
    grid-template-columns: 1fr;
  }
}

@media screen and (max-width: 480px) {
  .home__title {
    font-size: 2rem;
  }

  .home__subtitle {
    font-size: 1rem;
  }

  .home__buttons {
    flex-direction: column;
  }

  .button {
    width: 100%;
  }

  .section__title {
    font-size: 1.75rem;
  }

  .about__heading,
  .contact__heading {
    font-size: 1rem;
  }

  .contact__container {
    padding: 0 1rem;
  }

  .nav__container {
    padding: 0 1rem;
  }
}